@import '../Utility/base.scss';
// 输入框
.#{$variable}-input {
  display: inline-block;
  box-sizing: border-box;
  vertical-align: middle;
  height: 30px;
  padding-left: 10px;
  padding-right: 30px;
  width: 150px;
  font-size: 12px;
  border: 1px solid;
  border-radius: 0;
  color: #444;
  transition: 0.2s ease-in-out;
  transition-property: color, background-color, border;
  outline: none;
  font-family: inherit;
  background-color: transparent;
  @include border-color('border-color');
  @include themeBgcolor('themeBgcolor');
  &::-webkit-input-placeholder {
    color: #bbb;
  }

  &::-moz-placeholder {
    color: #bbb;
  }

  &:-ms-input-placeholder {
    color: #bbb;
  }

  &:-moz-placeholder {
    color: #bbb;
  }

  &:hover {
    border-color: #bbb;
    outline: 0;
  }

  &:focus {
    border-color: #006eff;
    outline: 0;
  }

  &:disabled {
    background-color: #f2f2f2;
    color: #bbb;
    background-image: none;
    cursor: not-allowed;
  }

  &:disabled:focus,
  &:disabled:hover {
    border-color: #ddd;
  }

  &[readonly='readonly'],
  &[readonly] {
    background-color: #f2f2f2;
    color: #444;
  }

  &[readonly='readonly']:focus,
  &[readonly='readonly']:hover,
  &[readonly]:focus,
  &[readonly]:hover {
    outline: 0 none;
    border-color: #ddd;
  }
  &.is-error {
    @include error();
  }
}
.is-error .#{$variable}-input {
  @include error();
}
