@import '../Utility/base.scss';
// 文案
.#{$variable}-text {
  @include text-color('text-color');
  display: inline-block;
  @include font12();
  // 水平对齐
  &--center {
    text-align: center;
  }
  &--left {
    text-align: left;
  }
  &--right {
    text-align: right;
  }
  &--justify {
    text-align: justify;
  }
  // 垂直对齐
  &--top {
    vertical-align: top;
  }
  &--middle {
    vertical-align: middle;
  }
  &--bottom {
    vertical-align: bottom;
  }
  &--baseline {
    vertical-align: baseline;
  }
  &--text-top {
    vertical-align: text-top;
  }
  &--text-bottom {
    vertical-align: text-bottom;
  }
  // 颜色
  &--weak {
    // color: $text-color-weak;
    @include text-color('text-color-weak');
  }
  &--normal {
    // color: $text-color-normal;
    @include text-color('text-color-normal');
  }
  &--error {
    // color: $text-color-error;
    @include text-color('text-color-error');
  }
  &--success {
    // color: $text-color-success;
    @include text-color('text-color-success');
  }
  // 链接
  &--link {
    color: $primary;
    &:hover {
      color: $primary-hover;
    }
  }
}
// 溢出行数
@for $i from 1 through 20 {
  .#{$variable}-text--overflow#{$i} {
    white-space: normal;
    -webkit-box-orient: vertical;
    display: -webkit-box;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: $i;
    @include font12();
    max-height: 20px * $i;
  }
  .#{$variable}-text {
    &-box#{$i} {
      position: relative;
      overflow: hidden;
      @include font12();
      max-height: 20px * $i;
    }
  }
  .#{$variable}-text-box#{$i} {
    .#{$variable}-text {
      &-box#{$i} {
        max-height: 20px * $i;
      }
      &__prepare {
        max-height: 20px * ($i);
      }
      &__line {
        top: 20px * ($i - 1);
        &-all {
          margin-top: -20px * ($i - 1);
        }
        &-overflow {
          -webkit-line-clamp: $i;
          max-height: 20px * $i;
          margin-top: -20px * ($i - 1);
        }
      }
    }
  }
}

// 带 更多的 溢出

.#{$variable}-text {
  &__btn {
    &-folded {
      display: inline-block;
    }
  }
  &__all {
    text-align: justify;
    color: transparent;
    a {
      color: inherit;
    }
  }
  &__prepare {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    overflow: hidden;
    text-align: justify;
  }
  &__line {
    position: absolute;
    left: 0;
    overflow: hidden;
    right: 0;
    &-all {
      position: relative;
      text-align: justify;
      left: 0;
      right: 0;
      color: transparent;
    }
    &-overflow {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
      color: #000;
      position: absolute;
      top: 0;
      background-color: #fff;
    }
    &-more {
      position: absolute;
      right: 0;
      top: 0;
      height: 100%;
      font-size: 12px;
      padding-left: 20px;
      background: linear-gradient(
        to right,
        rgba(255, 255, 255, 0) 0%,
        rgba(255, 255, 255, 0.1) 10%,
        rgba(255, 255, 255, 0.5) 20%,
        rgba(255, 255, 255, 1) 34%,
        white 100%
      );
    }
  }
}
.is-actived {
  &.#{$variable}-text {
    max-height: initial;
    .fl-text__all {
      color: inherit;
      a {
        @include text-color('text-color-primary');
      }
    }
    .fl-text__line,
    .fl-text__prepare {
      display: none;
    }
  }
}
