@import '../Utility/base.scss';
[class*='#{$variable}-col-'] .#{$variable}-media {
  height: 100%;
}
.#{$variable}-media {
  text-align: center;
  box-sizing: border-box;
  &.card {
    padding: 20px;
    background-color: #fff;
  }
  &.border {
    border: 1px solid;
    @include border-color('border-color');
  }
  &.shadow {
    box-shadow: 0 0px 5px rgba(0, 0, 0, 0.12), 0 5px 10px rgba(0, 0, 0, 0.12);
  }
  &.motion {
    transition: transform 0.2s linear;
    cursor: pointer;
    &:hover {
      transform: translate3d(0, -3px, 0);
    }
  }
  &__img {
    font-size: 0;
    img {
      max-width: 100%;
    }
  }
  &__info {
    width: 80%;
    margin: 12px auto 0;
    &-title {
      @include font14();
      @include text-color('text-color');
    }
    &-des {
      margin-top: 8px;
      @include font12();
      @include text-color('text-color-weak');
    }
  }
}
