@import '../Utility/base.scss';
.#{$variable}-card {
  box-shadow: 0 0px 5px rgba(0, 0, 0, 0.12), 0 5px 10px rgba(0, 0, 0, 0.12);
  @include themeBlock('themeBlock');
  & + & {
    margin-top: 20px;
  }
  &__header {
    padding: 20px;
    position: relative;
    font-size: 14px;
    font-weight: 600;
  }
  &__body {
    padding: 20px;
    position: relative;
    font-size: 0;
  }
  &__foot {
    padding: 20px;
    position: relative;
  }
  &__header + &__body {
    &:before {
      content: '';
      position: absolute;
      left: 20px;
      right: 20px;
      top: 0;
      border-top: 1px solid #ddd;
    }
  }
  &__header + &__foot {
    &:before {
      content: '';
      position: absolute;
      left: 20px;
      right: 20px;
      top: 0;
      border-top: 1px solid #ddd;
    }
  }
  &__body + &__foot {
    &:before {
      content: '';
      position: absolute;
      left: 20px;
      right: 20px;
      top: 0;
      border-top: 1px solid #ddd;
    }
  }
}
