@import '../Utility/base.scss';
.#{$variable}-container {
  display: flex;
  width: 100%;
  flex-direction: column;
  position: relative;
  overflow-x: auto;
  &.is-full {
    height: 100%;
  }
  &__header {
    height: 60px;
    padding: 0 20px;
    box-sizing: border-box;
    position: relative;
    width: 100%;
    z-index: 9;
    min-width: 1200px;
    border-bottom: 1px solid;
    @include themeBgcolor('themeBgcolor');
    @include themeBorderColor('themeBorderColor');
    color: #333;

    &-inner {
      position: relative;
      height: 100%;
      font-size: 0;
      display: flex;
    }
    &-logo {
      display: flex;
      align-items: center;
      &--img {
        height: 30px;
        img {
          height: 100%;
        }
      }
      &--title {
        display: inline-block;
        vertical-align: middle;
        color: inherit;
        line-height: 1.6;
        font-size: 22px;
        letter-spacing: 1px;
        font-weight: 500;
      }
      &--img + &--title {
        margin-left: 10px;
      }
    }
  }
  &__body {
    @include themeBgcolor('themeBgcolor');
    font-size: 0;
    display: flex;
    flex: 1;
    min-height: 0;
    min-width: 1200px;
    &-inner {
      position: relative;
      min-height: 100%;
      display: flex;
      width: 100%;
      margin: 0 auto;
    }
  }
  @media screen and (max-width: 768px) {
    &__header,
    &__body {
      min-width: initial;
    }
  }
}
