@import '../Utility/base.scss';
.#{$variable}-icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  @include text-color('text-color-weak');
  font-size: 0;
  svg {
    width: 100%;
    height: 100%;
  }
}
// 内外间距
@for $i from 8 through 80 {
  .icon-#{$i} {
    width: $i * 1px;
    height: $i * 1px;
  }
}
@mixin icon($url) {
  background-image: url($url);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.#{$variable}-icon + .#{$variable}-icon {
  margin-left: 12px;
}
// 可点击
.is-hover {
  cursor: pointer;
  &:hover {
    color: $primary;
  }
}
