@import '../Utility/base.scss';

.#{$variable}-tab {
  &__list {
    display: flex;
    border-bottom: 1px solid;
    @include border-color('border-color');
  }
  &--ceiling {
    margin-top: -63px;
    padding-top: 0.1px;
    > .#{$variable}-tab__list {
      padding: 0 20px;
      position: absolute;
      left: 0;
      right: 0;
      margin-bottom: 20px;
    }
    > .#{$variable}-tab__main {
      padding-top: 62px;
      margin-top: 0;
    }
  }
  &__list-item {
    height: 26px;
    line-height: 26px;
    padding: 8px 8px 8px 8px;
    text-align: center;
    font-size: 14px;
    font-weight: 500;
    @include text-color('text-color');
    position: relative;
    transition: color 0.25s ease-in-out;
    cursor: pointer;

    &:after {
      content: '';
      display: block;
      position: absolute;
      left: 50%;
      bottom: -1px;
      z-index: 1;
      width: 10%;
      height: 2px;
      background-color: transparent;
      transform: translateX(-50%);
    }
    &:hover,
    &.is-actived {
      @include text-color('text-color-hover');
      transition: all 0.2s ease-in-out;
      font-weight: 600;
    }
    &.is-actived {
      &:after {
        width: calc(100% - 16px);
        @include btn-bgcolor('btn-bgcolor-primary');
      }
    }
    &:first-child {
      padding-left: 0;
    }
    &:first-child.is-actived:after {
      margin-left: -4px;
      width: calc(100% - 8px);
    }
    & + .#{$variable}-tab__list-item {
      margin-left: 16px;
    }
  }
  &__main {
    margin-top: 20px;
  }
  &__panel {
    display: none;
    font-size: 12px;
    @include text-color('text-color');
    &.is-actived {
      display: block;
    }
  }
}
.#{$variable}-card .#{$variable}-tab {
  margin-top: -10px;
}
