//////////// 页面布局 s
html {
  font-family: 'pingfang SC', 'helvetica neue', arial, 'hiragino sans gb',
    'microsoft yahei ui', 'microsoft yahei', simsun, sans-serif;
  font-size: 14px;
  -webkit-tap-highlight-color: transparent; //是一个没有标准化的属性，能够设置点击链接的时候出现的高亮颜色。显示给用户的高光是他们成功点击的标识，以及暗示了他们点击的元素。初始值	black
  word-break: break-all; // 始终断行
  word-break: break-word; //以一个单词或数字显示不下断行处理
  //仅在无法将整个单词放在自己的行而不会溢出的情况下才会产生, word-wrap 属性原本属于微软的一个私有属性，在 CSS3 现在的文本规范草案中已经被重名为 overflow-wrap 。 word-wrap 现在被当作 overflow-wrap 的 “别名”。 稳定的谷歌 Chrome 和 Opera 浏览器版本支持这种新语法。
  word-wrap: break-word;
}
body {
  -webkit-font-smoothing: antialiased; //平滑像素级别的字体，而不是子像素。从亚像素渲染切换到黑暗背景上的浅色文本的抗锯齿使其看起来更轻
}
a {
  text-decoration: none;
}
li {
  list-style: none;
}
body,
button,
dd,
div,
dl,
dt,
form,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
input,
li,
ol,
p,
td,
textarea,
th,
ul {
  margin: 0;
  padding: 0;
}
html,
body,
#root {
  position: relative;
  height: 100%;
  width: 100%;
}
