@import '../Utility/base.scss';
.#{$variable}-table {
  background-color: #fff;
  box-sizing: border-box;
  &__header {
    border-bottom: 1px solid #ddd;
    overflow: hidden;
  }
  &__body {
    border-bottom: 1px solid #e5e5e5;
    overflow: auto;
  }
  &__box {
    border-collapse: collapse;
    border-spacing: 0;
    table-layout: fixed;
    width: 100%;
    position: relative;
    th {
      text-align: left;
      vertical-align: middle;
      position: relative;
      padding: 10px;
    }
    td {
      border-bottom: 1px solid #e5e5e5;
      color: #444;
      padding: 14px 10px;
      height: auto;
      // &.rowspan {
      //   border-right: 1px solid #e5e5e5;
      //   border-left: 1px solid #e5e5e5;
      // }
      // &:first-child {
      //   border-left: none;
      // }
      // &:last-child {
      //   border-right: none;
      // }
    }
    tr:last-child td {
      border-bottom: none;
    }
  }
}
