@import '../Utility/base.scss';
html {
  scroll-behavior: smooth;
}
.#{$variable}-layout__content {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-width: 0;
  min-height: 0;
  position: relative;
  @include text-color('text-color');
  &-header {
    border-bottom: 1px solid;
    @include themeBgcolor('themeBgcolor');
    @include themeBorderColor('themeBorderColor');
    padding: 20px 20px 20px;
    flex-shrink: 0;
    flex-grow: 0;
    &--title {
      font-size: 16px;
      font-weight: 600;
    }
    &.ceiling {
      padding-bottom: 30px;
    }
    &.ceiling:not(:empty) {
      padding-bottom: 50px;
    }
  }
  &-body {
    @include themeContent('themeContent');
    margin-top: 0;
    font-size: 12px;
    box-sizing: border-box;
    position: relative;
    display: flex;
    flex-shrink: 1;
    flex-grow: 1;
    min-height: 0;
    // 二级导航
    &--sideMenu {
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      width: 180px;
    }
    // 滚动合子
    &--scroll {
      flex: 1;
      min-height: 0;
      min-width: 0;
      overflow: auto;
      box-sizing: border-box;
      scroll-behavior: smooth;
    }
    &--inner {
      padding: 20px;
      margin: 0 auto;
      max-width: 1360px;
      box-sizing: border-box;
    }
    &.is-full &--scroll {
      overflow: hidden;
    }
    &.is-full &--inner {
      max-width: 100%;
      height: 100%;
      overflow: auto;
      box-sizing: border-box;
      scroll-behavior: smooth;
      padding: 0;
    }
  }
  @media (min-width: 1920px) {
    .#{$variable}-layout__content {
      &-body {
        &--inner {
          max-width: 80%;
        }
      }
    }
  }
}

// 开启二级菜单 重置 sideMenu 组件
.has-subMenu {
  position: relative;
  .#{$variable}-layout__content-body--scroll {
    padding-left: 180px;
  }
  .#{$variable}-sideMenu {
    width: 100%;
    &__item {
      &-title {
        font-size: 12px;
        padding-left: 12px;
        padding-right: 12px;
      }
    }
  }

  @media screen and (max-width: 768px) {
    & .#{$variable}-layout__content-body--sideMenu {
      width: 160px;
    }
    & .#{$variable}-layout__content-body--scroll {
      padding-left: 0;
    }
  }
}
