@import '../Utility/base.scss';
.#{$variable}-radio {
  &__item {
    display: inline-block;
    font-size: 0;
    cursor: pointer;

    & + & {
      margin-left: 12px;
    }
    &.is-actived {
      color: $primary;
    }
    &.is-actived &-dot {
      &--des {
        @include themeBgcolor('themeBgcolor');
      }
    }
    &-dot {
      display: inline-block;
      vertical-align: middle;
      position: relative;
      &--des {
        display: block;
        background-color: transparent;
        width: 8px;
        height: 8px;
        border-radius: 100%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
    &-text {
      line-height: 16px;
      cursor: pointer;
      font-size: 12px;
      display: inline-block;
      vertical-align: middle;
      margin-left: 4px;
    }
  }
  // 按钮风格

  &--btn {
    .#{$variable}-radio {
      &__item {
        white-space: nowrap;
        border: 1px solid;
        @include themeBgcolor('themeBgcolor');
        @include border-color('border-color');
        border-left: 0;
        @include text-color('text-color');
        -webkit-appearance: none;
        text-align: center;
        box-sizing: border-box;
        outline: 0;
        margin: 0;
        cursor: pointer;
        transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
        padding: 8px 16px;
        font-size: 12px;
        border-radius: 0;
        &-dot {
          opacity: 0;
          display: none;
        }
        &-text {
          margin: 0;
        }
        &:first-child {
          border-left: 1px solid;
          @include border-color(border-color);
          border-radius: 4px 0 0 4px;
          box-shadow: none !important;
        }
        &:last-child {
          border-radius: 0 4px 4px 0;
        }
        &.is-actived,
        &.is-actived:first-child {
          background-color: $primary;
          border-color: $primary;
          box-shadow: -1px 0 0 0 $primary;
          color: #fff;
          font-weight: 600;
        }
      }
    }
  }
}

// radio
.#{$variable}-input--radio {
  width: 16px;
  height: 16px;
  cursor: pointer;
  vertical-align: middle;
  outline: none;
  -webkit-appearance: none;
  border: 1px solid #cfd5de;
  border-radius: 100%;
  background-position: inherit;
  color: rgba(0, 0, 0, 0.9);
  box-sizing: border-box;
  padding: 0;
  &:checked {
    background-color: $primary;
    border: 1px solid $primary;
  }
}
