@import '../Utility/base.scss';
.#{$variable}-animation {
  font-size: 0;
  // CSS 加载 - 双圈
  &__loading {
    position: relative;
    width: 50px;
    height: 50px;
    display: inline-block;
    &-big,
    &-small {
      box-sizing: border-box;
      position: absolute;
      background-color: transparent;
      border: 5px solid $primary;
      opacity: 0.9;
      border-right: 5px solid transparent;
      border-left: 5px solid transparent;
      border-radius: 50px;
    }
    &-big {
      // box-shadow: 0 0 35px grey;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      animation: loadingBig 1s infinite ease-in-out;
    }
    &-small {
      // box-shadow: 0 0 15px #202020;
      width: 30px;
      height: 30px;
      left: 10px;
      top: 10px;
      animation: loadingSmall 1s infinite linear;
    }
    @keyframes loadingBig {
      0% {
        -webkit-transform: rotate(0deg);
      }
      100% {
        -webkit-transform: rotate(1turn);
      }
    }
    @keyframes loadingSmall {
      0% {
        -webkit-transform: rotate(0deg);
      }
      100% {
        -webkit-transform: rotate(-1turn);
      }
    }
  }
  // CSS 加载 - 点转圈
  &__loadingDot {
    font-size: 20px;
    color: #fff;
    position: relative;
    height: 62px;
    width: 62px;
    display: inline-block;
    &-item {
      height: 48px;
      width: 48px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

      .circle {
        position: absolute;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background-color: #0052d9;
        animation-fill-mode: both;
        opacity: 1;
        animation: circleTurn 1.2s infinite ease-in-out;
        &:nth-child(0) {
          left: 0;
          top: 0;
        }

        &:nth-child(1) {
          right: 0;
          top: 0;
          animation-delay: -0.9s;
        }

        &:nth-child(2) {
          bottom: 0;
          right: 0;
          animation-delay: -0.6s;
        }

        &:nth-child(3) {
          animation-delay: -0.3s;
          bottom: 0;
          left: 0;
        }
      }
      &.circle-box2 {
        transform: translate(-50%, -50%) rotateZ(45deg);
        .circle {
          &:nth-child(0) {
            animation-delay: -1.1s;
          }
          &:nth-child(1) {
            animation-delay: -0.8s;
          }
          &:nth-child(2) {
            animation-delay: -0.5s;
          }
          &:nth-child(3) {
            animation-delay: -0.2s;
          }
        }
      }
    }
    @keyframes circleTurn {
      0%,
      80%,
      100% {
        transform: scale(0);
        -webkit-transform: scale(0);
      }

      40% {
        transform: scale(1);
        -webkit-transform: scale(1);
      }
    }
  }
  // CSS 音乐跳动
  &__music {
    display: inline-block;
    height: 18px;
    line-height: 18px;
    text-align: center;
    position: relative;
    vertical-align: bottom;
    font-size: 0;

    &-item {
      display: inline-block;
      vertical-align: middle;
      width: 2px;
      margin-bottom: 0;
      background-color: $primary;
      margin-right: 6px;
      &:nth-child(4n-3) {
        animation: music 0.6s infinite ease-in-out alternate;
      }
      &:nth-child(4n-2) {
        animation: music 0.6s 0.2s infinite ease-in-out alternate;
      }
      &:nth-child(4n-1) {
        animation: music 0.6s 0.4s infinite ease-in-out alternate;
      }
      &:nth-child(4n) {
        animation: music 0.6s 0.6s infinite ease-in-out alternate;
      }
    }
    @keyframes music {
      from,
      10% {
        height: 4px;
      }
      to,
      90% {
        height: 18px;
      }
    }
  }
  // CSS 加载 - 点点点
  &__dot {
    &-item {
      background-color: #3372ff;
      width: 12px;
      height: 12px;
      display: inline-block;
      vertical-align: middle;
      animation: dot 1.17s infinite;
      opacity: 0;
      & + & {
        margin-left: 12px;
      }
      &.is-circle {
        border-radius: 50%;
      }
    }
    @for $i from 1 through (50) {
      .#{$variable}-animation__dot-item:nth-child(#{$i}) {
        animation-delay: 0.13 * ($i) + s;
      }
    }
    @keyframes dot {
      0% {
        opacity: 1;
      }
      100% {
        opacity: 0;
      }
    }
  }
  // CSS 扫描
  &__scanning {
    height: 100px;
    width: 200px;
    background-color: rgba(51, 114, 255, 0.7);
    position: relative;
    overflow: hidden;
    &-inner {
      position: absolute;
      bottom: 0;
      left: 0;
      height: 50%;
      width: 100%;
      background: linear-gradient(
        0deg,
        rgba(120, 240, 135, 0.01) 0%,
        rgba(120, 240, 135, 0.5) 80%
      );
      animation: scanning 3s infinite ease-in-out;
    }
    &.is-about &-inner {
      background: linear-gradient(
        90deg,
        rgba(120, 240, 135, 0.01) 0%,
        rgba(120, 240, 135, 0.5) 80%
      );
      height: 100%;
      width: 50%;
      animation: scanningAbout 3s infinite ease-in-out;
    }
    @keyframes scanning {
      0% {
        bottom: 0;
        transform: rotateX(0deg) translateY(100%);
      }

      49.9999% {
        bottom: 100%;
        transform: rotateX(0deg) translateY(0);
      }

      50% {
        bottom: 100%;
        transform: rotateX(180deg) translateY(0);
      }

      99.9999% {
        bottom: 0;
        transform: rotateX(180deg) translateY(-100%);
      }

      100% {
        bottom: 0;
        transform: rotateX(0deg) translateY(100%);
      }
    }
    @keyframes scanningAbout {
      0% {
        left: 0;
        transform: rotateZ(0deg) translateX(-100%);
      }
      49.9999% {
        left: 100%;
        transform: rotateZ(0deg) translateX(0);
      }
      50% {
        left: 100%;
        transform: rotateZ(180deg) translateX(0);
      }

      99.9999% {
        left: 0;
        transform: rotateZ(180deg) translateX(100%);
      }

      100% {
        left: 0;
        transform: rotateZ(0deg) translateX(100%);
      }
    }
  }
  // Svg 进度条 - 圆形
  &__svgCircle {
    display: inline-block;
    position: relative;
    &-value {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      color: #000;
      font-size: 16px;
      font-weight: 500;
    }
  }
  // js 进度条 - 圆形
  &__jsCircle {
    font-size: 0;
    display: inline-block;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: #ebf0ff;
    position: relative;
    overflow: hidden;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 50%;
      bottom: 0;
      background-color: inherit;
      overflow: hidden;
      z-index: 2;
    }
    &::after {
      content: '';
      position: absolute;
      left: 4px;
      right: 4px;
      top: 4px;
      bottom: 4px;
      border-radius: 50%;
      overflow: hidden;
      background-color: inherit;
      box-sizing: border-box;
      z-index: 8;
    }
    &-bar {
      position: absolute;
      top: 0;
      left: 0;
      width: 50%;
      bottom: 0;
      height: 100%;
      transform-origin: right center;
      z-index: 1;
      background-color: #07c160;
      &__inner {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-top-left-radius: 50%;
        border-bottom-left-radius: 50%;
        overflow: hidden;
        background-color: inherit;
        transform-origin: right center;
        z-index: 1;
        // transform: rotate(179deg);
      }
    }
    &-value {
      position: absolute;
      z-index: 9;
      font-size: 16px;
      line-height: 24px;
      left: 50%;
      top: 50%;
      height: auto;
      margin: 0;
      display: inline-block;
      width: auto;
      transform: translate(-50%, -50%);
      color: #07c160;
    }
  }
  // Js 进度条 - 斑马纹
  &__striped {
    height: 22px;
    border-radius: 12px;
    overflow: hidden;
    position: relative;
    background: rgb(36, 36, 36); /* Old browsers */
    background: -moz-linear-gradient(
      top,
      rgba(36, 36, 36, 1) 0%,
      rgba(38, 38, 38, 1) 20%,
      rgba(40, 40, 40, 1) 21%,
      rgba(42, 42, 42, 1) 33%,
      rgba(46, 46, 46, 1) 38%,
      rgba(50, 50, 50, 1) 55%,
      rgba(50, 50, 50, 1) 100%
    ); /* FF3.6-15 */
    background: -webkit-linear-gradient(
      top,
      rgba(36, 36, 36, 1) 0%,
      rgba(38, 38, 38, 1) 20%,
      rgba(40, 40, 40, 1) 21%,
      rgba(42, 42, 42, 1) 33%,
      rgba(46, 46, 46, 1) 38%,
      rgba(50, 50, 50, 1) 55%,
      rgba(50, 50, 50, 1) 100%
    ); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(
      to bottom,
      rgba(36, 36, 36, 1) 0%,
      rgba(38, 38, 38, 1) 20%,
      rgba(40, 40, 40, 1) 21%,
      rgba(42, 42, 42, 1) 33%,
      rgba(46, 46, 46, 1) 38%,
      rgba(50, 50, 50, 1) 55%,
      rgba(50, 50, 50, 1) 100%
    ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    &-value {
      height: 100%;
      position: relative;
      overflow: hidden;
      border-radius: 12px;
      width: 0;
      transition: width 1s;
      background: $primary;
      background-image: linear-gradient(
        -45deg,
        rgba(255, 255, 255, 0.15) 25%,
        transparent 25%,
        transparent 50%,
        rgba(255, 255, 255, 0.15) 50%,
        rgba(255, 255, 255, 0.15) 75%,
        transparent 75%,
        transparent
      );
      background-size: 30px 30px;

      &::before {
        content: '';
        height: 100%;
        width: 100%;
        left: 0;
        background: linear-gradient(
          0deg,
          rgba(255, 255, 255, 0.02),
          rgba(255, 255, 255, 0.6)
        );
        position: absolute;
      }
    }
    &.is-start &-value {
      animation: striped 2s linear infinite;
    }
    @keyframes striped {
      0% {
        background-position: 0 0;
      }
      100% {
        background-position: 30px 0;
      }
    }
  }
  // Js 进度条 - 水波纹
  &__ripple {
    width: 100px;
    height: 100px;
    border: 2px solid rgba(76, 171, 255, 0.8);
    border-radius: 50%;
    position: relative;
    overflow: hidden;
    z-index: 1;
    background: rgba(76, 171, 255, 0.4);
    &-bg {
      width: 200%;
      height: 200%;
      position: absolute;
      background: rgba(76, 171, 255, 0.5);
      background: radial-gradient(
        circle at 100px 100px,
        rgba(76, 171, 255, 0.8),
        #163344
      );
      border-radius: 40%;
      top: 100%;
      left: -50%;
      animation: rippleTurn 5s linear infinite;
    }
    &-value {
      width: 100%;
      top: 50%;
      position: absolute;
      font-size: 30px;
      color: #071820;
      text-align: center;
      -webkit-transform: translate(0, -50%);
      transform: translate(0, -50%);
    }
    @keyframes rippleTurn {
      from {
        transform: rotate(0deg);
      }
      to {
        transform: rotate(360deg);
      }
    }
  }
  // 水滴
  &__waterDrop {
    position: relative;
    background-size: cover;
    &-img {
      &::before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #fff2;
        -webkit-backdrop-filter: blur(4px);
        backdrop-filter: blur(4px);
      }
      img {
        width: 100%;
      }
    }
    &-item {
      position: absolute;
      bottom: 20px;
      left: 20px;
      width: 40px;
      height: 32px;
      border-radius: 50% 50% 50% 50% / 50% 50% 45% 45%;
      background: radial-gradient(ellipse at center, white 40%, transparent 70%)
        no-repeat 60% 25% / 16% 15%;
      box-shadow: inset 7px 0 2px -5px rgba(0, 0, 0, 0.2),
        inset 6px 0 1px -5px rgba(0, 0, 0, 0.2),
        inset 0 -1px 2px rgba(250, 241, 220, 0.5),
        inset 0 -10px 5px 1px rgba(255, 255, 255, 0.3),
        inset -10px 5px 2px -10px rgba(0, 0, 0, 0.3),
        inset -10px 7px 5px -10px rgba(0, 0, 0, 0.2),
        inset 0 10px 15px -2px rgba(0, 0, 0, 0.3),
        0 2px 1px -1px rgba(245, 227, 183, 0.5),
        -1px 8px 1px -2px rgba(0, 0, 0, 0.5),
        -1px 10px 2px -2px rgba(0, 0, 0, 0.4),
        1px -1px 2px 0 rgba(50, 50, 50, 0.5);
    }
  }
}
