@import '../Utility/base.scss';
.#{$variable}-checkout {
  font-size: 0;
  &__item {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    padding-left: 24px;
    margin-top: 0;
    margin-right: 18px;
    min-height: 16px;
    color: rgba(0, 0, 0, 0.9);
    cursor: pointer;
    &-input {
      cursor: pointer;
      width: 16px;
      height: 16px;
      vertical-align: middle;
      outline: none;
      -webkit-appearance: none;
      border: 1px solid;
      @include border-color(border-color);
      @include themeBgcolor('themeBgcolor');
      border-radius: 0;
      color: rgba(0, 0, 0, 0.9);
      position: absolute;
      top: 0;
      left: 0;
      &:checked,
      &:checked:focus,
      &:checked:hover {
        background-image: url(data:image/svg+xml;base64,);
        background-color: $primary;
        border-color: $primary;
        background-position: inherit;
        background-size: 100% auto;
      }
    }
    &-text {
      line-height: 16px;
      cursor: pointer;
      font-size: 12px;
      user-select: none;
      @include text-color('text-color');
    }
  }
}
