}

@mixin showWidth() {
  max-width: 1180px;
  margin: 0 auto;
}
// 开方
@function sqrt($num) {
  $temp: 1;
  @while abs($temp - $num / $temp) > 1e-6 {
    $temp: ($temp + $num/$temp) / 2;
  }
  @return $temp;
}
// 幂操作
@function pow($number, $exp) {
  $value: 1;
  @if $exp > 0 {
    @for $i from 1 through $exp {
      $value: $value * $number;
    }
  } @else if $exp < 0 {
    @for $i from 1 through -$exp {
      $value: $value / $number;
    }
  }
  @return $value;
}
// 阶乘
@function fact($number) {
  $value: 1;
  @if $number > 0 {
    @for $i from 1 through $number {
      $value: $value * $i;
    }
  }
  @return $value;
}
// π 的约值
@function pi() {
  @return 3.14159265359;
}
@function rad($angle) {
  $unit: unit($angle);
  $unitless: $angle / ($angle * 0 + 1);
  // If the angle has 'deg' as unit, convert to radians.
  @if $unit == deg {
    $unitless: $unitless / 180 * pi();
  }
  @return $unitless;
}
// sin 值
@function sin($angle) {
  $sin: 0;
  $angle: rad($angle);
  // Iterate a bunch of times.
  @for $i from 0 through 10 {
    $sin: $sin + pow(-1, $i) * pow($angle, (2 * $i + 1)) / fact(2 * $i + 1);
  }
  @return $sin;
}
// cos 值
@function cos($angle) {
  $cos: 0;
  $angle: rad($angle);
  // Iterate a bunch of times.
  @for $i from 0 through 10 {
    $cos: $cos + pow(-1, $i) * pow($angle, 2 * $i) / fact(2 * $i);
  }
  @return $cos;
}
// tan 值
@function tan($angle) {
  @return sin($angle) / cos($angle);
}
// 直角三角型斜边值
@function hypotenuse() {
  $w: 0.5 * $CssTiangleWidth;
  $c: $CssTiangleHeight;
  $h: sqrt(pow($c, 2) - pow($w, 2));
  @return $h;
}
