@import '../Utility/base.scss';
.#{$variable}-avatar {
  width: 60px;
  height: 60px;
  display: inline-block;
  background-color: #f0f5f9;
  border: 1px solid #ddd;
  border-radius: 4px;
  // padding: 6px;
  overflow: hidden;
  &__list {
    padding: 0;
    margin: 0;
    list-style-type: none;
    width: 100%;
    height: 100%;
    display: inline-block;
    vertical-align: middle;
    &-item {
      background-size: 100%;
      outline: 1px solid #fff;
      /* 1个 */
      &:only-child {
        height: 100%;
      }
      /* 2个 */
      &:first-child:nth-last-child(2),
      &:first-child:nth-last-child(2) + & {
        width: 50%;
        height: 50%;
      }
      &:first-child:nth-last-child(2) + & {
        margin-left: auto;
      }
      /* 3个 */
      &:first-child:nth-last-child(3),
      &:first-child:nth-last-child(3) ~ & {
        width: 50%;
        height: 50%;
      }
      &:first-child:nth-last-child(3) {
        margin: auto;
      }
      &:first-child:nth-last-child(3) ~ li {
        float: left;
      }
      /* 4个 */
      &:first-child:nth-last-child(4),
      &:first-child:nth-last-child(4) ~ li {
        width: 50%;
        height: 50%;
        float: left;
      }
      /* 5个 */
      &:first-child:nth-last-child(5) {
        width: 33.33%;
        height: 33.33%;
        float: left;
        margin-left: 16.66%;
        margin-top: 16.66%;
      }
      &:first-child:nth-last-child(5) + & {
        margin-top: 16.66%;
      }
      &:first-child:nth-last-child(5) ~ & {
        width: 33.33%;
        height: 33.33%;
        float: left;
      }
      /* 6个 */
      &:first-child:nth-last-child(6) {
        width: 33.33%;
        height: 33.33%;
        float: left;
        margin-top: 16.66%;
      }
      &:first-child:nth-last-child(6) + &,
      &:first-child:nth-last-child(6) + & + & {
        margin-top: 16.66%;
      }

      &:first-child:nth-last-child(6) ~ & {
        width: 33.33%;
        height: 33.33%;
        float: left;
      }
      /* 7个 8个 9个 */
      &:first-child:nth-last-child(7),
      &:first-child:nth-last-child(8),
      &:first-child:nth-last-child(9),
      &:first-child:nth-last-child(7) ~ &,
      &:first-child:nth-last-child(8) ~ &,
      &:first-child:nth-last-child(9) ~ & {
        width: 33.33%;
        height: 33.33%;
        float: left;
      }
      &:first-child:nth-last-child(7) {
        float: none;
        margin: auto;
      }
      &:first-child:nth-last-child(8) {
        margin-left: 16.66%;
      }
    }
  }
}
