@import '../Utility/base.scss';
.#{$variable}-form {
  display: table;
  // 居中
  &--middle &__item-label,
  &--middle &__item-info,
  &__item-middle &__item-label,
  &__item-middle &__item-info {
    vertical-align: middle;
  }
  // top
  &--top &__item-label,
  &--top &__item-info,
  &__item-top &__item-label,
  &__item-top &__item-info {
    vertical-align: top;
  }
  // bottom
  &--bottom &__item-label,
  &--bottom &__item-info,
  &__item-bottom &__item-label,
  &__item-bottom &__item-info {
    vertical-align: bottom;
  }
  // 最后一行 padding-bottom
  &__item:last-child &__item-label,
  &__item:last-child &__item-info {
    padding-bottom: 0;
  }
  &__item {
    display: table-row;
    &-label,
    &-info {
      display: table-cell;
      @include font14();
      vertical-align: top;
      padding-bottom: 16px;
    }
    &-label {
      @include form-text-color('form-label-color');
      white-space: nowrap;
      padding-right: 16px;
      label {
        display: block;
        line-height: 30px;
      }
    }
    &-info {
      @include form-text-color('form-info-color');
      text-align: left;
      line-height: 30px;
      font-size: 0;
      color: #888;
      &--text {
        line-height: initial;
        margin-top: 4px;
        @include font14();
        line-height: 30px;
        &:first-child {
          margin-top: 0;
        }
      }
      &.is-error {
        @include error();
      }
    }
  }
}
