@import '../Utility/base.scss';
.#{$variable}-layout__content-header {
  .#{$variable}-anchor {
    margin-bottom: -24px;
  }
}
.#{$variable}-anchor {
  &.card {
    background: #ffffff;
    box-shadow: 0 0px 5px rgba(0, 0, 0, 0.12), 0 5px 10px rgba(0, 0, 0, 0.12);
    .#{$variable}-anchor__item {
      min-height: 60px;
    }
  }
  &.ceiling {
    position: absolute;
    margin-top: -20px;
    transform: translateY(-100%);
    width: 100%;
    box-sizing: border-box;
    left: 0;
  }
  &.fixed {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 99;
    margin: 0;
    transform: none;
    background-color: #ffffff;
    box-shadow: 0 0px 5px rgba(0, 0, 0, 0.12), 0 5px 10px rgba(0, 0, 0, 0.12);
    .#{$variable}-anchor__item {
      min-height: 52px;
    }
  }
  &.aside {
    height: 100%;
    width: 236px;
    box-sizing: border-box;
    padding: 16px 0;
    background-color: #fff;
    .#{$variable}-anchor {
      &__list {
        display: flex;
        flex-direction: column;
        padding-left: 0;
        overflow-y: auto;
        overflow-x: hidden;
        max-height: 100%;
      }
      &__item {
        padding: 8px 20px;
        margin-right: 0;
        font-size: 14px;
        font-weight: 500;
        text-align: left;
        justify-content: flex-start;
        &::before {
          display: none;
        }
        &:hover {
          color: $primary;
        }
        &.is-actived {
          background-color: rgba(0, 0, 0, 0.06);
        }
      }
    }
  }
  &__list {
    display: flex;
    overflow: auto;
    white-space: nowrap;
    padding-left: 20px;
  }
  &__item {
    @include text-color('text-color-weak');
    font-size: 16px;
    padding: 0 12px 8px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 12px;

    &:last-child {
      margin-right: 0;
      padding-right: 20px;
    }

    &::before {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      height: 2px;
    }

    &.is-actived {
      color: $primary;
      &::before {
        background-color: $primary;
      }
    }
  }
}
