// 组件前缀
$variable: 'fl';
// 变量公共主题色
$primary: #0068ed;
$primary-hover: #0070ff;
$themes: (
  light: (
    // 面板色
    themeBgcolor: #fff,
    themeContent: #f2f2f2,
    themeBlock: #fff,
    themeBorderColor: #ddd,
    // text
    text-color: #000,
    text-color-error: #e54545,
    text-color-success: #0abf5b,
    text-color-normal: #333,
    text-color-weak: #666666,
    text-color-primary: $primary,
    text-color-hover: $primary-hover,
    // border
    border-color: #e5e5e5,
    border-color-hover: $primary-hover,
    // 表单
    form-label-color: #888,
    form-info-color: #333,
    // btn 字体
    btn-text-color: #000,
    btn-text-color-hover: #fff,
    btn-text-color-primary: #fff,
    btn-text-color-primary-hover: #fff,
    // btn 边框
    btn-border-color: #e5e5e5,
    btn-border-color-hover: $primary,
    btn-border-color-primary: $primary,
    btn-border-color-primary-hover: $primary-hover,
    // btn 背景
    btn-bgcolor: #fff,
    btn-bgcolor-weak-hover: #f5f5f5,
    btn-bgcolor-hover: $primary,
    btn-bgcolor-primary: $primary,
    btn-bgcolor-primary-hover: $primary-hover,
    // select
    select-bgcolor: #fff,
    select-bgcolor-hover: rgba(0, 0, 0, 0.06),
  ),
  dark: (
    // 面板色
    themeBgcolor: #2c2c2c,
    themeContent: #181818,
    themeBlock: #2c2c2c,
    themeBorderColor: rgba(255, 255, 255, 0.3),
    // 变量公共主题色
    primary: #0068ed,
    primary-hover: #0070ff,
    // text
    text-color: rgba(255, 255, 255, 1),
    text-color-normal: rgba(255, 255, 255, 0.9),
    text-color-weak: rgba(255, 255, 255, 0.7),
    text-color-error: #e54545,
    text-color-success: #0abf5b,
    text-color-hover: #0068ed,
    // border
    border-color: rgba(255, 255, 255, 0.3),
    border-color-hover: #0070ff,
    // 表单
    form-label-color: #888,
    form-info-color: #333,
    // btn 字体
    btn-text-color: #000,
    btn-text-color-hover: #fff,
    btn-text-color-primary: #ffff,
    btn-text-color-primary-hover: #fff,
    // btn 边框
    btn-border-color: rgba(255, 255, 255, 0.3),
    btn-border-color-hover: #0068ed,
    btn-border-color-primary: #0068ed,
    btn-border-color-primary-hover: #0070ff,
    // btn 背景
    btn-bgcolor: #fff,
    btn-bgcolor-hover: #0068ed,
    btn-bgcolor-weak-hover: #f5f5f5,
    btn-bgcolor-primary: #0068ed,
    btn-bgcolor-primary-hover: #0070ff,
    // select
    select-bgcolor: #2c2c2c,
    select-bgcolor-hover: transparent,
  ),
);
//遍历主题map
@mixin themeify {
  @each $theme-name, $theme-map in $themes {
    //!global 把局部变量强升为全局变量
    $theme-map: $theme-map !global;
    //判断html的data-theme的属性值  #{}是sass的插值表达式
    //& sass嵌套里的父容器标识   @content是混合器插槽，像vue的slot
    [data-theme*='#{$theme-name}'] & {
      @content;
    }
  }
}
//声明一个根据Key获取颜色的function
@function themed($key) {
  @return map-get($theme-map, $key);
}
// ///////
// 面板色
@mixin themeBgcolor($color) {
  @include themeify {
    background-color: themed($color);
  }
}
@mixin themeBorderColor($color) {
  @include themeify {
    border-color: themed($color);
  }
}
@mixin themeBlock($color) {
  @include themeify {
    background-color: themed($color);
  }
}
@mixin themeContent($color) {
  @include themeify {
    background-color: themed($color);
  }
}

//border
@mixin border-color($color) {
  @include themeify {
    border-color: themed($color);
  }
}
//text
@mixin text-color($color) {
  @include themeify {
    color: themed($color);
  }
}
// 表单
@mixin form-text-color($color) {
  @include themeify {
    color: themed($color);
  }
}
// btn 字体
@mixin btn-text-color($color) {
  @include themeify {
    color: themed($color);
  }
}
// btn 边框
@mixin btn-border-color($color) {
  @include themeify {
    border-color: themed($color);
  }
}
// btn 背景
@mixin btn-bgcolor($color) {
  @include themeify {
    background-color: themed($color);
  }
}

// select 背景
@mixin select-bgcolor($color) {
  @include themeify {
    background-color: themed($color);
  }
}
