@import '../Utility/base.scss';
.#{$variable}-datepicker {
  display: inline-block;
  vertical-align: middle;
  user-select: none;
  &__input {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    .fl-icon {
      position: absolute;
      right: 8px;
      margin: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  .#{$variable}-input {
    cursor: pointer;
  }
}

// 日期
.#{$variable}-calendar {
  &__top {
    display: flex;
    align-items: center;
    padding: 10px 12px 10px 8px;
    border-bottom: 1px solid #ddd;
    user-select: none;
    &-text {
      flex-shrink: 1;
      flex-grow: 1;
      a {
        font-weight: 700;
        color: #000;
      }
    }
    &-op {
      font-size: 0;
    }
  }
  &__header {
    display: flex;
  }
  &__body {
    display: flex;
    align-items: stretch;
  }
  &__row {
    display: flex;
    flex: auto;
    font-size: 0;
    flex-wrap: wrap;
  }
  &__cell {
    text-align: center;
    vertical-align: middle;
    padding: 0 8px;
    white-space: nowrap;
    cursor: pointer;
    > span {
      display: inline-block;
      min-width: 30px;
      height: 30px;
      line-height: 30px;
      border-radius: 100%;
      font-size: 13px;
      position: relative;
      z-index: 1;
      user-select: none;
    }
    .is-selected &,
    &.is-selected span {
      background-color: #006eff;
      color: #fff;
    }
  }
  &__age,
  &__month,
  &__day {
    padding: 4px 0;
    letter-spacing: 0.8px;
    overflow-y: scroll;
    overflow-x: hidden;
    flex-shrink: 0;
    flex-grow: 0;
    min-height: 186px;
    max-height: 210px;
    border-left: 1px solid #ddd;
    border-right: 1px solid #ddd;
    margin-left: -1px;
    position: relative;
  }
  &__day {
    width: 282px;
    min-height: 186px;
    max-height: initial;
    border-right: none;
  }
  &__day &__cell {
    padding: 0 4px;
  }
  &__day &__cell.is-selected {
    border-radius: 50%;
  }
}
