@import '../Utility/base.scss';
.#{$variable}-mediaBanner {
  position: relative;
  &__content {
    position: relative;
    margin-top: -15px;
    &-inner {
      overflow: hidden;
      transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
    }
    // action
    &-action {
      &--next,
      &--prev {
        margin: 0 15px;
        position: absolute;
        width: 50px;
        height: 50px;
        background: #ffffff;
        border-radius: 28px;
        top: 4vw;
        z-index: 1;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: color 0.25s;
        transition: all 0.25s;
        opacity: 0.9;
        box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.09);
        font-size: 20px;
        @include text-color('text-color-weak');
        &:hover {
          color: $primary;
          opacity: 1;
          box-shadow: 0 10px 20px 0 rgba(18, 38, 64, 0.15);
        }
      }
      &--prev {
        left: 0;
        top: 50%;
        transform: translate(-50%, -50%) rotateZ(180deg);
      }
      &--next {
        right: 0;
        top: 50%;
        transform: translate(50%, -50%);
      }
    }
  }
  &__list {
    white-space: nowrap;
    display: flex;
    flex-wrap: nowrap;
    // transition: transform 2s ease-in-out, -webkit-transform 2s ease-in-out;
  }
  &__item {
    display: flex;
    white-space: normal;
    width: 100%;
    flex-shrink: 0;
    flex-grow: 0;
    box-sizing: border-box;
    padding: 15px;
    &-inner {
      width: 100%;
      height: 100%;
    }
    .#{$variable}-row {
      height: 100%;
    }
  }
  // dot
  &__dot {
    text-align: center;
    margin-top: 20px;
    font-size: 0;
    &-item {
      width: 40px;
      height: 16px;
      display: inline-block;
      margin: 0 10px;
      cursor: pointer;
      align-items: center;
      &::before {
        content: '';
        display: block;
        width: 100%;
        height: 4px;
        background-color: #cdcdcd;
        margin-top: 6px;
      }
      &.is-actived::before {
        background-color: $primary;
      }
    }
    // 只移动端显示
    &.mobile {
      display: none;
    }
    // pc、移动都显示
    &.show {
      display: block;
    }
  }
}
