@import '../Utility/base.scss';
.#{$variable}-popover {
  position: relative;
  display: inline-block;
}
.#{$variable}-popover .#{$variable}-bubble {
  // display: none;
  position: absolute;
  z-index: 999;
}
.#{$variable}-popover:hover .#{$variable}-bubble {
  display: block;
}
.#{$variable}-popover {
  &-start {
    top: 0;
    transform: translate(100%, 0);
  }
  &-center {
    top: 50%;
    transform: translate(100%, -50%);
  }
  &-end {
    top: 100%;
    transform: translate(100%, -100%);
  }
  &-top {
    top: 100%;
    left: 0;
    transform: translate(0%, 10px);
    &::before {
      top: -10px;
      left: 0;
      right: 0;
      bottom: 100%;
    }
    &.fl-bubble-center {
      left: 50%;
      transform: translate(-50%, 10px);
    }
    &.fl-bubble-end {
      left: 100%;
      transform: translate(-100%, 10px);
    }
  }
  &-bottom {
    top: -10px;
    left: 0;
    transform: translate(0%, -100%);
    &::before {
      width: auto;
      top: 100%;
      left: 0;
      right: 0;
      bottom: -10px;
    }
    &.#{$variable}-bubble-center {
      left: 50%;
      transform: translate(-50%, -100%);
    }
    &.#{$variable}-bubble-end {
      left: 100%;
      transform: translate(-100%, -100%);
    }
  }
  &-left {
    right: -10px;
    top: 0;
    transform: translateX(100%);
    &:before {
      position: absolute;
      top: 0;
      left: -10px;
      bottom: 0;
      right: 100%;
    }
  }
  &-right {
    top: 0;
    left: -10px;
    transform: translate(-100%, 0);
    &::before {
      top: 0;
      left: 100%;
      right: -10px;
      bottom: 0;
    }
    &.#{$variable}-bubble-center {
      top: 50%;
      transform: translate(-100%, -50%);
    }
    &.#{$variable}-bubble-end {
      top: 100%;
      transform: translate(-100%, -100%);
    }
  }
}
// 弹出层
.#{$variable}-overlay {
  z-index: 999;
  &__inner {
    border: 1px solid #ddd;
    background-color: #fff;
    box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.2);
    min-width: 150px;
    box-sizing: border-box;
    margin-top: 10px;
    margin-bottom: 10px;
  }
}
