@import '../Utility/base.scss';
.#{$variable}-select {
  display: inline-block;
  font-size: 0;
  &.size-s {
    width: 80px;
  }
  &.size-m {
    width: 150px;
  }
  &.size-l {
    width: 200px;
  }
  &.size-xl {
    width: 400px;
  }
  &.size-full {
    width: 100%;
  }
  &__content {
    display: flex;
    align-items: center;
    padding: 4px 8px;
    border: 1px solid;
    @include border-color(border-color);
    @include themeBgcolor('themeBgcolor');
    box-sizing: border-box;
    cursor: pointer;
    &-text {
      flex-shrink: 1;
      flex-grow: 1;
      min-width: 0;
      @include font12();
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      -webkit-line-clamp: 1;
      &.color-weak {
        @include text-color('text-color-weak');
      }
    }
  }
  &__icon {
    flex-shrink: 0;
    flex-grow: 0;
    padding-left: 10px;
  }
  &__list {
    max-height: 300px;
    overflow-y: auto;
    overflow-x: hidden;
  }
  &__item {
    padding: 4px 8px;
    cursor: pointer;
    &:hover {
      // background-color: $btn-bgcolor-weak-hover;
      @include btn-bgcolor('btn-bgcolor-weak-hover');
    }
    &.is-actived {
      // background-color: $primary;
      @include btn-bgcolor('btn-bgcolor-primary');
      color: #fff;
    }
  }
}
