@import '../Utility/base.scss';
.#{$variable}-bubble {
  position: relative;
  display: inline-block;
  line-height: initial;
  min-width: 120px;
  visibility: hidden;
  opacity: 0;
  z-index: -1;
  &.is-actived {
    visibility: visible;
    opacity: 1;
    z-index: 1;
  }
  &::before {
    content: '';
    position: absolute;
  }
  &__inner {
    font-size: 12px;
    color: #333;
    box-sizing: border-box;
    padding: 16px;
    border-radius: 0;
    background-color: #fff;
    line-height: 1.5;
    box-shadow: 0 -5px 10px rgba(0, 0, 0, 0.12), 0 5px 10px rgba(0, 0, 0, 0.12);
    position: static !important;
    white-space: normal;
    word-wrap: break-word;

    &:before,
    &:after {
      font-size: 0;
      position: absolute;
      width: 0;
      height: 0;
      content: '';
      border: 6px solid transparent;
    }
  }

  &-top &__inner {
    left: 50%;
    bottom: 100%;
  }
  &-top &__inner:before {
    left: inherit;
    bottom: inherit;
    right: inherit;
    margin-left: -6px;
    border-bottom-color: #ddd;
  }
  &-top &__inner:after {
    left: inherit;
    bottom: inherit;
    right: inherit;
    margin-left: -6px;
    border-bottom-color: #fff;
    margin-bottom: -1px;
  }
  &-top#{&}-start &__inner {
    left: 15px;
  }
  &-top#{&}-end &__inner {
    left: auto;
    right: 15px;
  }
  &-top#{&}-start &__inner:before,
  &-top#{&}-start &__inner:after,
  &-top#{&}-end &__inner:before,
  &-top#{&}-end &__inner:after {
    margin-left: 0;
  }

  &-bottom &__inner {
    left: 50%;
    top: 100%;
  }
  &-bottom &__inner:before {
    left: inherit;
    right: inherit;
    top: inherit;
    margin-left: -6px;
    border-top-color: #ddd;
  }
  &-bottom &__inner:after {
    left: inherit;
    right: inherit;
    top: inherit;
    margin-left: -6px;
    border-top-color: #fff;
    margin-top: -1px;
  }
  &-bottom#{&}-start &__inner {
    left: 15px;
  }
  &-bottom#{&}-end &__inner {
    left: auto;
    right: 15px;
  }
  &-bottom#{&}-start &__inner:before,
  &-bottom#{&}-start &__inner:after,
  &-bottom#{&}-end &__inner:before,
  &-bottom#{&}-end &__inner:after {
    margin-left: 0;
  }

  &-left &__inner {
    top: 50%;
    right: 100%;
  }
  &-left &__inner:before {
    top: inherit;
    bottom: inherit;
    right: inherit;
    margin-top: -6px;
    border-right-color: #ddd;
  }
  &-left &__inner:after {
    top: inherit;
    bottom: inherit;
    right: inherit;
    margin-top: -6px;
    border-right-color: #fff;
    margin-right: -1px;
  }
  &-left#{&}-start &__inner {
    top: 15px;
  }
  &-left#{&}-end &__inner {
    top: auto;
    bottom: 15px;
  }
  &-left#{&}-start &__inner:before,
  &-left#{&}-start &__inner:after,
  &-left#{&}-end &__inner:before,
  &-left#{&}-end &__inner:after {
    margin-top: 0;
  }

  &-right &__inner {
    top: 50%;
    left: 100%;
  }
  &-right &__inner:before {
    top: inherit;
    bottom: inherit;
    left: inherit;
    margin-top: -6px;
    border-left-color: #ddd;
  }
  &-right &__inner:after {
    top: inherit;
    bottom: inherit;
    left: inherit;
    margin-top: -6px;
    border-left-color: #fff;
    margin-left: -1px;
  }
  &-right#{&}-start &__inner {
    top: 15px;
  }
  &-right#{&}-end &__inner {
    top: auto;
    bottom: 15px;
  }
  &-right#{&}-start &__inner:before,
  &-right#{&}-start &__inner:after,
  &-right#{&}-end &__inner:before,
  &-right#{&}-end &__inner:after {
    margin-top: 0;
  }

  // 异常提示
  &-error &__inner {
    background-color: #fbe3e3;
    border-color: #fbe3e3;
    color: #be3b38;
    box-shadow: none;
  }

  &-error#{&}-top &__inner:before,
  &-error#{&}-top &__inner:after {
    border-bottom-color: #fbe3e3;
  }
  &-error#{&}-bottom &__inner:before,
  &-error#{&}-bottom &__inner:after {
    border-top-color: #fbe3e3;
  }
  &-error#{&}-left &__inner:before,
  &-error#{&}-left &__inner:after {
    border-right-color: #fbe3e3;
  }
  &-error#{&}-right &__inner:before,
  &-error#{&}-right &__inner:after {
    border-left-color: #fbe3e3;
  }

  // 黑色
  &-dark &__inner {
    background-color: #333;
    border-color: #333;
    color: #fff;
    box-shadow: none;
  }

  &-dark#{&}-top &__inner:before,
  &-dark#{&}-top &__inner:after {
    border-bottom-color: #333;
  }
  &-dark#{&}-bottom &__inner:before,
  &-dark#{&}-bottom &__inner:after {
    border-top-color: #333;
  }
  &-dark#{&}-left &__inner:before,
  &-dark#{&}-left &__inner:after {
    border-right-color: #333;
  }
  &-dark#{&}-right &__inner:before,
  &-dark#{&}-right &__inner:after {
    border-left-color: #333;
  }

  // 触发文字（下虚线的形式）
  &-text {
    color: #333;
    border-bottom: 1px dashed #ddd;
    cursor: pointer;
  }
}
