@import '../Utility/base.scss';
// 弹出层
.#{$variable}-popover__warp {
  z-index: -1;
  &.is-actived {
    z-index: 999;
  }
}
.#{$variable}-popover {
  position: relative;
  display: inline-block;
}
.#{$variable}-popover .#{$variable}-bubble {
  position: absolute;
  z-index: 999;
}
.#{$variable}-popover:hover .#{$variable}-bubble {
  display: block;
}
.#{$variable}-popover:hover .#{$variable}-bubble.is-click {
  display: none;
}
.#{$variable}-popover {
  .#{$variable}-bubble-start {
    top: 0;
    transform: translate(100%, 0);
  }
  .#{$variable}-bubble-center {
    top: 50%;
    transform: translate(100%, -50%);
  }
  .#{$variable}-bubble-end {
    top: 100%;
    transform: translate(100%, -100%);
  }
  .#{$variable}-bubble-top {
    top: 100%;
    left: 0;
    transform: translate(0%, 10px);
    &::before {
      top: -10px;
      left: 0;
      right: 0;
      bottom: 100%;
    }
    &.#{$variable}-bubble-center {
      left: 50%;
      transform: translate(-50%, 10px);
    }
    &.#{$variable}-bubble-end {
      left: 100%;
      transform: translate(-100%, 10px);
    }
  }
  .#{$variable}-bubble-bottom {
    top: -10px;
    left: 0;
    transform: translate(0%, -100%);
    &::before {
      width: auto;
      top: 100%;
      left: 0;
      right: 0;
      bottom: -10px;
    }
    &.#{$variable}-bubble-center {
      left: 50%;
      transform: translate(-50%, -100%);
    }
    &.#{$variable}-bubble-end {
      left: 100%;
      transform: translate(-100%, -100%);
    }
  }
  .#{$variable}-bubble-left {
    right: -10px;
    top: 0;
    transform: translateX(100%);
    &:before {
      position: absolute;
      top: 0;
      left: -10px;
      bottom: 0;
      right: 100%;
    }
  }
  .#{$variable}-bubble-right {
    top: 0;
    left: -10px;
    transform: translate(-100%, 0);
    &::before {
      top: 0;
      left: 100%;
      right: -10px;
      bottom: 0;
    }
    &.#{$variable}-bubble-center {
      top: 50%;
      transform: translate(-100%, -50%);
    }
    &.#{$variable}-bubble-end {
      top: 100%;
      transform: translate(-100%, -100%);
    }
  }
}
