@import '../Utility/base.scss';
$borderWidth: 1px;
.#{$variable}-button {
  display: inline-block;
  vertical-align: middle;
  line-height: 28px;
  font-size: 12px;
  box-sizing: border-box;
  height: 30px;
  padding: 0 12px;
  min-width: 80px;
  text-align: center;
  border: $borderWidth solid;
  @include btn-text-color('btn-text-color');
  @include btn-border-color('btn-border-color');
  @include btn-bgcolor('btn-bgcolor');
  outline: none;
  cursor: pointer;
  font-weight: 500;
  box-sizing: border-box;
  user-select: none;
  position: relative;
  z-index: 0;
  &::before {
    content: '';
    display: block;
    background: inherit;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 3px;
    left: 3px;
    filter: blur(6px);
    opacity: 0.6;
    z-index: -1;
    transform-origin: 0 0;
    border-radius: inherit;
    transform: scale(1, 1);
  }
  & + & {
    margin-left: 12px;
  }
  &:hover {
    @include btn-text-color('btn-text-color-hover');
    @include btn-border-color('btn-border-color-hover');
    @include btn-bgcolor('btn-bgcolor-primary-hover');
    transition: 0.2s all;
  }
  &--primary {
    @include btn-text-color('btn-text-color-primary');
    @include btn-border-color('btn-border-color-primary');
    @include btn-bgcolor('btn-bgcolor-primary');
    &:hover {
      @include btn-text-color('btn-text-color-primary-hover');
      @include btn-border-color('btn-border-color-hover');
      @include btn-bgcolor('btn-bgcolor-primary-hover');
      transition: 0.2s all;
    }
  }
  &--empty {
    height: auto;
    line-height: initial;
    background: transparent !important;
    border: none;
    min-width: initial;
    padding: 0;
    @include text-color('text-color');
    &:hover {
      background: transparent !important;
      text-decoration: underline;
      @include text-color('text-color');
    }
  }
  &--empty.#{$variable}-button--primary {
    @include text-color('text-color-hover');
    box-shadow: none;
  }
  // 水波
  .ripple-effect {
    overflow: hidden;
    position: absolute;
    top: -$borderWidth;
    left: -$borderWidth;
    right: -$borderWidth;
    bottom: -$borderWidth;
    z-index: -1;
    &__inner {
      border-radius: 50%;
      background: rgba(0, 0, 0, 0.4);
      -webkit-transform: scale(0);
      transform: scale(0);
      position: absolute;
      opacity: 1;

      &.is-actived {
        animation: rippleEffect 2.5s cubic-bezier(0.23, 1, 0.32, 1);
      }
    }
  }
}
@keyframes rippleEffect {
  100% {
    -webkit-transform: scale(2);
    transform: scale(2);
    opacity: 0;
  }
}
