// 菜单
@import '../Utility/base.scss';
.#{$variable}-sideMenu {
  width: 236px;
  display: inline-block;
  z-index: 9;
  @include themeBgcolor('themeBgcolor');
  @include text-color('text-color');
  @include themeBorderColor('themeBorderColor');
  border-right: 1px solid;
  box-sizing: border-box;
  user-select: none;
  height: 100%;
  display: flex;
  flex-direction: column;
  &__title + &__list {
    padding-top: 0;
  }
  &__title {
    font-size: 16px;
    line-height: 22px;
    font-weight: 600;
    padding: 20px 0;
    margin: 0 20px;
    margin-bottom: 16px;
    border-bottom: 1px solid;
    @include themeBorderColor('themeBorderColor');
  }
  &__list {
    flex-shrink: 1;
    flex-grow: 1;
    height: 20px;
    padding-top: 16px;
    padding-bottom: 16px;
    overflow-y: auto;
    overflow-x: hidden;
    &::-webkit-scrollbar {
      width: 6px;
      height: 6px;
    }
    &::-webkit-scrollbar-thumb {
      background: rgb(171, 171, 171);
      border-radius: 20px;
    }
    &::-webkit-scrollbar-track {
      border-radius: 20px;
    }
  }
  &__item {
    @include text-color('text-color-normal');
    display: block;
    &.is-actived,
    &:hover {
      color: $primary;
    }
    &.is-actived {
      font-weight: 500;
      @include select-bgcolor('select-bgcolor-hover');
    }
    &-title {
      display: flex;
      align-items: center;
      padding: 8px 20px;
      border-left: 2px solid transparent;
      border-right: 2px solid transparent;
      color: inherit;
      font-size: 14px;
      cursor: pointer;
    }
  }
  @media screen and (max-width: 768px) {
    position: absolute;
    width: 160px;
    &__bg {
      content: '';
      position: absolute;
      left: 100%;
      right: -20000000px;
      bottom: 0;
      top: 0;
      background-color: rgba(0, 0, 0, 0.5);
    }
    &__title {
      font-size: 14px;
    }
    &__item {
      &-title {
        font-size: 12px;
      }
    }
    &.is-hidden {
      display: none;
    }
    &.is-hidden + .fl-layout__content & {
      display: none;
    }
    .has-subMenu & {
      left: 159px;
      box-sizing: content-box;
      padding-bottom: 64px;
      margin-top: -64px;
      @include themeBorderColor('themeBorderColor');
      border-left: 1px solid;
      &__bg {
        display: none;
      }
    }
  }
}
@media screen and (max-width: 768px) {
  .has-subMenu .#{$variable}-layout__content-body--sideMenu {
    width: 160px;
  }
  .has-subMenu .#{$variable}-layout__content-body--scroll {
    padding-left: 0;
  }
}
